import '../../sass/HomePage.scss';
import React from 'react';
import Layout from '../../../../apps/components/AppLayout';
import UParams from '../../../../assets/libs/uparams';
import classNames from 'classnames';
// import moment from 'moment';
//import { Toast, Modal } from 'antd-mobile';
const { Header, Content } = Layout;
class CardDetails extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isDetail: true
        };
    }
    componentWillMount() {
    }
    pageDetail() {
        return (<div className='content'>
                <div className='card'>
                    <div className='c-left'></div>
                    <div className='c-right'></div>
                    <div className='c-icon icon-pz'></div>
                    <div className='c-name'></div>
                    <div className='c-number'>4481545154</div>

                    <div className='row'>
                        <div className='left fl'></div>
                        <div className='right fr'></div>
                    </div>
                    <div className='row'>
                        <div className='left fl'></div>
                        <div className='right fr'></div>
                    </div>
                    <div className='row'>
                        <div className='left fl'></div>
                        <div className='right fr'>2018.01.12 11:30</div>
                    </div>
                    <div className='row'>
                        <div className='left fl'></div>
                        <div className='right fr'>3 7</div>
                    </div>

                    <div className='row-2 clearfix'>
                        <div className='left fl'></div>
                        <div className='right fr'>
                            <div className='item'> x1</div>
                            <div className='item'> x2</div>
                            <div className='item'> x10</div>
                        </div>
                    </div>

                    <div className='row-2 clearfix'>
                        <div className='left fl'></div>
                        <div className='right fr'>
                            <div className='text'></div>
                        </div>
                    </div>

                </div>
            </div>);
    }
    pageRecord() {
        return (<div className='content'>
                <div className='list'>
                    <div className='name'></div>
                    <div className='i-1'></div>
                    <div className='num'>10</div>
                    <div className='time'>2018.01.12 11:30</div>
                    <div className='line'></div>
                </div>

                <div className='list'>
                    <div className='name'></div>
                    <div className='i-1'></div>
                    <div className='num'>10</div>
                    <div className='time'>2018.01.12 11:30</div>
                    <div className='line'></div>
                </div>

            </div>);
    }
    render() {
        let parms = UParams();
        const BtnDel = () => {
            return (<div className='btn-del'></div>);
        };
        const BtnPush = () => {
            return (<div className='btn-push' onClick={() => window.location.href = '#/choose?type=2?shopid=' + this.props.mchId + '?cardid=' + parms.cardid}></div>);
        };
        return (<Layout>
                <Header title=''/>
                <Content>
                    <div className="wrap clearfix" data-page='details'>

                        <div className='headbar'>
                            <div className={classNames('btn btn-1', { 'active': this.state.isDetail == true })} onClick={() => this.setState({ isDetail: true })}></div>
                            <div className={classNames('btn btn-2', { 'active': this.state.isDetail == false })} onClick={() => this.setState({ isDetail: false })}></div>
                        </div>

                        {this.state.isDetail ? this.pageDetail() : this.pageRecord()}

                        <BtnDel />
                        <BtnPush />

                    </div>

                </Content>
            </Layout>);
    }
}
export default CardDetails;



