import '../../sass/HomePage.scss';
import React from 'react';
import Layout from '../../../../apps/components/AppLayout';
//import UParams from '../../../../assets/libs/uparams';
import classNames from 'classnames';
// import moment from 'moment';
//import { Toast } from 'antd-mobile';
const { Header, Content } = Layout;
class CardList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currState: 1,
            pageNumber: 0,
            pageSize: 999
        };
    }
    componentWillMount() {
    }
    getData(status) {
        this.setState({
            currState: status,
        });
    }
    render() {
        const HeadUlLi = (props) => {
            return (<div className={classNames('btn', { "active": this.state.currState === props.value })} onClick={() => this.getData(props.value)}>
                {props.name}
            </div>);
        };
        const marketItemList = () => {
            return (<div>
                    <div className='card' onClick={() => window.location.href = '#/getconfig?cardid=159?shopid=' + this.props.mchId}>
                        <div className='icon'></div>
                        <div className='status'></div>
                        <div className='line name'>1</div>
                        <div className='line num'>3194788454514</div>
                        <div className='line l-1'>100</div>
                        <div className='line l-2'>100</div>
                        <div className='line l-3'>2018.01.10-1120</div>
                        <div className='line l-4'>2018.01.10  2018.02.01</div>
                    </div>

                    <div className='card over'>
                        <div className='stamp'></div>
                        <div className='icon'></div>
                        <div className='status'></div>
                        <div className='line name'>1</div>
                        <div className='line num'>3194788454514</div>
                        <div className='line l-1'>100</div>
                        <div className='line l-2'>100</div>
                        <div className='line l-3'>2018.01.10-1120</div>
                        <div className='line l-4'>2018.01.10  2018.02.01</div>
                    </div>
                </div>);
        };
        return (<Layout>
                <Header title='' addIsShow={true} addUrl={'selfcard'}/>
                <Content>
                    <div className="wrap clearfix" data-page='selfcardlist'>

                        <div className='headbar'>
                            <HeadUlLi value={1} name={""}/>
                            <HeadUlLi value={2} name={""}/>
                            <HeadUlLi value={3} name={""}/>
                        </div>

                        <div className='content'>
                            {marketItemList()}
                        </div>

                    </div>

                </Content>
            </Layout>);
    }
}
export default CardList;



