import '../../sass/HomePage.scss';
import React from 'react';
class AddPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    componentWillMount() {
    }
    render() {
        return (<div className="wrap clearfix" data-page='add'>

                <div className='card card-ping'>
                    <div className='name'></div>
                    <div className='text'></div>
                    <div className='btn' onClick={() => window.location.href = "#/setpz?mchid=" + this.props.mchId}></div>
                </div>

                <div className='card card-dai'>
                    <div className='name'></div>
                    <div className='text'></div>
                    <div className='btn' onClick={() => window.location.href = "#/setdj?mchid=" + this.props.mchId}></div>
                </div>

                <div className='card card-man'>
                    <div className='name'></div>
                    <div className='text'></div>
                    <div className='btn' onClick={() => window.location.href = "#/setmj?mchid=" + this.props.mchId}></div>
                </div>

            </div>);
    }
}
export default AddPage;



